@keyframes arrow-bounce {
/* 	0%, 20%, 50%, 80%, 100% {
	transform: scale(1.1);
	opacity: .5;
	} */
	0%{
		opacity: 1;
	}
	50% {
		opacity: .15;
	}
	100% {
		opacity: 1;
	}
}



@keyframes smoothScroll {
	0% {
		transform: translateY(-40px);
	}
	100% {
		transform: translateY(0px);
	}
}